import React from "react";
import { useSelector, useDispatch } from "react-redux";

export default function Settings(props) {
  const exercice = useSelector((state) => state.exercice);
  const interval = useSelector((state) => state.interval);
  const active = useSelector((state) => state.active);
  const passive = useSelector((state) => state.passive);
  const pause = useSelector((state) => state.pause);
  const lap = useSelector((state) => state.lap);

  const dispatch = useDispatch();

  /////////////////////////// interval
  const moreInt = () => {
    dispatch({
      type: "MORE-INT",
    });
    props.res();
  };
  const lessInt = () => {
    dispatch({
      type: "LESS-INT",
    });
    props.res();
  };

  //////////////////////////// active
  const moreAct = () => {
    dispatch({
      type: "MORE-ACT",
    });
    props.res();
  };
  const lessAct = () => {
    dispatch({
      type: "LESS-ACT",
    });
    props.res();
  };

  //////////////////////////// passive
  const morePas = () => {
    dispatch({
      type: "MORE-PAS",
    });
    props.res();
  };
  const lessPas = () => {
    dispatch({
      type: "LESS-PAS",
    });
    props.res();
  };

  //////////////////////////// pause
  const moreRes = () => {
    dispatch({
      type: "MORE-PAU",
    });
    props.res();
  };
  const lessRes = () => {
    dispatch({
      type: "LESS-PAU",
    });
    props.res();
  };

  //////////////////////////// lap
  const moreLap = () => {
    dispatch({
      type: "MORE-LAP",
    });
    props.res();
  };
  const lessLap = () => {
    dispatch({
      type: "LESS-LAP",
    });
    props.res();
  };

  return (
    <div className="container-settings">
      <div className="setting">
        <i className="fas fa-sliders-h"></i>
      </div>

      <div className="updates">
        <div className="title-update">
          <div className="title">intervalles :</div>
          <div className="update interval">
            <button onClick={lessInt}> - </button>{" "}
            <div className="num">{interval}</div>
            <button onClick={moreInt}>+</button>
          </div>
        </div>
        <div className="title-update">
          <div className="title">actif :</div>
          <div className="update active">
            <button onClick={lessAct}> - </button>{" "}
            <div className="num">{active}s</div>{" "}
            <button onClick={moreAct}>+</button>
          </div>
        </div>
        <div className="title-update">
          <div className="title">repos : </div>
          <div className="update passive">
            <button onClick={lessPas}> - </button>{" "}
            <div className="num">{passive}s</div>{" "}
            <button onClick={morePas}>+</button>
          </div>
        </div>
        <div className="title-update">
          <div className="title">pause :</div>
          <div className="update rest">
            <button onClick={lessRes}> - </button>{" "}
            <div className="num">{pause}s</div>{" "}
            <button onClick={moreRes}>+</button>
          </div>
        </div>
        <div className="title-update">
          <div className="title">tour :</div>
          <div className="update lap">
            <button onClick={lessLap}> - </button>{" "}
            <div className="num">{lap}</div>{" "}
            <button onClick={moreLap}>+</button>
          </div>
        </div>
      </div>
    </div>
  );
}
